    display: flex;
    flex-direction: row;
    justify-content: center; }
    .activity__icon svg {
      height: 100%; }
      .activity__icon svg path {
        fill: #923649; }
      .activity__icon svg circle {
        fill: #EA5B79; }
  .activity__title {
    margin: 0 50px; }
  .activity__details {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 30px 25px;
    text-align: center; }
    .activity__details__time p span {
      font-weight: bold; }
    .activity__details .detail-container {
      display: flex;
      flex-direction: column;
      justify-content: center; }
      .activity__details .detail-container__icon {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        vertical-align: middle;
        height: 40px; }
        .activity__details .detail-container__icon div {
