      .activity__details .detail-container__label {
        font-size: 12px;
        line-height: 18px; }
  .activity__description {
    line-height: 24px;
    text-align: justify;
    margin: 0 50px; }
  .activity__steps {
    margin: 30px 50px; }
    .activity__steps__step {
      display: flex;
      flex-direction: row;
      margin-bottom: 30px; }
      .activity__steps__step__number {
        width: 90px;
        min-width: 90px;
        max-width: 90px;
        height: 90px;
        border-radius: 100px;
        margin-right: 20px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background: #EA5B79; }
        .activity__steps__step__number span {
